'use client';
import { useState, useEffect } from 'react';
import { createClient } from '@supabase/supabase-js';
import { motion, AnimatePresence } from 'framer-motion';

const supabase = createClient(
  process.env.NEXT_PUBLIC_SUPABASE_URL!,
  process.env.NEXT_PUBLIC_SUPABASE_ANON_KEY!
);

export default function IntelFeed() {
  const [intel, setIntel] = useState<any[]>([]);
  const [expanded, setExpanded] = useState<number | null>(null);

  useEffect(() => {
    fetchIntel();

    // üîÑ Live listener
    const channel = supabase
      .channel('intel-updates')
      .on(
        'postgres_changes',
        { event: '*', schema: 'public', table: 'intel_feed' },
        (payload) => {
          if (payload.new) setIntel((prev) => [payload.new, ...prev]);
        }
      )
      .subscribe();

    return () => {
      supabase.removeChannel(channel);
    };
  }, []);

  async function fetchIntel() {
    const { data, error } = await supabase
      .from('intel_feed')
      .select('*')
      .order('created_at', { ascending: false });
    if (!error && data) setIntel(data);
  }

  return (
    <div className="space-y-4 w-full">
      <AnimatePresence>
        {intel.map((item) => (
          <motion.div
            key={item.id}
            layout
            onClick={() => setExpanded(expanded === item.id ? null : item.id)}
            className="p-4 rounded-xl bg-slate-900 hover:bg-slate-800 cursor-pointer border border-slate-800"
          >
            <div className="flex justify-between items-center">
              <h3 className="text-blue-400 font-semibold text-sm">
                {item.headline}
              </h3>
              <span
                className={`text-xs px-2 py-1 rounded-full ${
                  item.sentiment === 'Positive'
                    ? 'bg-green-800 text-green-300'
                    : item.sentiment === 'Neutral'
                    ? 'bg-slate-700 text-slate-300'
                    : 'bg-red-800 text-red-300'
                }`}
              >
                {item.sentiment}
              </span>
            </div>
            <p className="text-gray-400 text-xs mt-1">{item.summary}</p>

            {/* üåê Expandable insight panel */}
            {expanded === item.id && (
              <motion.div
                initial={{ opacity: 0, height: 0 }}
                animate={{ opacity: 1, height: 'auto' }}
                exit={{ opacity: 0, height: 0 }}
                className="mt-2 p-3 rounded-lg bg-slate-800/60 text-xs text-gray-300 space-y-1"
              >
                <p>
                  <strong>Confidence:</strong> {item.confidence ?? '‚Äî'} %
                </p>
                <p>
                  <strong>Impact:</strong> {item.impact}
                </p>
                {item.insights && (
                  <p className="italic text-slate-400">‚Äú{item.insights}‚Äù</p>
                )}
                <p className="text-[10px] text-right text-slate-500">
                  {new Date(item.created_at).toLocaleString()}
                </p>
              </motion.div>
            )}
          </motion.div>
        ))}
      </AnimatePresence>
    </div>
  );
}
