'use client';
import { useEffect, useState } from 'react';
import { motion, AnimatePresence } from 'framer-motion';
import { createClient } from '@supabase/supabase-js';

const supabaseUrl = process.env.NEXT_PUBLIC_SUPABASE_URL!;
const supabaseAnonKey = process.env.NEXT_PUBLIC_SUPABASE_ANON_KEY!;
const supabase = createClient(supabaseUrl, supabaseAnonKey);

type Trend = {
  region: string;
  sentiment_level: string;
  avg_confidence: number;
  avg_impact: number;
  count: number;
};

export default function TrendPanel() {
  const [trends, setTrends] = useState<Trend[]>([]);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    fetchTrends();

    const channel = supabase
      .channel('intel-feed-trends')
      .on(
        'postgres_changes',
        { event: '*', schema: 'public', table: 'intel_feed' },
        () => fetchTrends()
      )
      .subscribe();

    return () => {
      supabase.removeChannel(channel);
    };
  }, []);

  async function fetchTrends() {
    setLoading(true);

    const { data, error } = await supabase
      .from('intel_feed')
      .select('region, sentiment_level, impact_score, confidence_score')
      .not('region', 'is', null);

    if (error) {
      console.error('TrendPanel fetch error:', error.message);
      setLoading(false);
      return;
    }

    const grouped: Record<
      string,
      { totalImpact: number; totalConfidence: number; count: number; sentimentScore: number }
    > = {};

    for (const row of data) {
      const region = row.region || 'Unknown';
      if (!grouped[region]) {
        grouped[region] = { totalImpact: 0, totalConfidence: 0, count: 0, sentimentScore: 0 };
      }

      grouped[region].totalImpact += row.impact_score || 0;
      grouped[region].totalConfidence += Number(row.confidence_score) || 0;
      grouped[region].sentimentScore +=
        row.sentiment_level === 'Positive'
          ? 1
          : row.sentiment_level === 'Negative'
          ? -1
          : 0;
      grouped[region].count += 1;
    }

    const result = Object.entries(grouped).map(([region, val]) => ({
      region,
      avg_confidence: Math.round(val.totalConfidence / val.count),
      avg_impact: Math.round(val.totalImpact / val.count),
      sentiment_level:
        val.sentimentScore > 0
          ? 'Positive'
          : val.sentimentScore < 0
          ? 'Negative'
          : 'Neutral',
      count: val.count,
    }));

    setTrends(result);
    setLoading(false);
  }

  return (
    <motion.div
      className="mt-6 w-full max-w-3xl bg-slate-900/80 rounded-2xl shadow-lg shadow-blue-900/20 p-5 border border-slate-800"
      initial={{ opacity: 0, y: 15 }}
      animate={{ opacity: 1, y: 0 }}
      transition={{ duration: 0.5 }}
    >
      <h2 className="text-lg font-semibold mb-3 text-blue-400 text-center">
        üìä Global Trend Panel
      </h2>

      {loading ? (
        <p className="text-slate-400 text-sm text-center">Loading live trends‚Ä¶</p>
      ) : trends.length === 0 ? (
        <p className="text-slate-500 text-sm text-center py-4">
          No trend data available yet.
        </p>
      ) : (
        <div className="grid grid-cols-1 md:grid-cols-2 gap-3">
          <AnimatePresence>
            {trends.map((t) => (
              <motion.div
                key={t.region}
                layout
                initial={{ opacity: 0, y: 10 }}
                animate={{ opacity: 1, y: 0 }}
                exit={{ opacity: 0, y: -10 }}
                transition={{ duration: 0.4 }}
                className="bg-slate-800 rounded-xl p-4 hover:bg-slate-700 transition"
              >
                <div className="flex justify-between items-center mb-2">
                  <span className="font-medium text-slate-200 capitalize">{t.region}</span>
                  <span
                    className={`text-xs px-2 py-1 rounded-full ${
                      t.sentiment_level === 'Positive'
                        ? 'bg-green-500/20 text-green-300'
                        : t.sentiment_level === 'Negative'
                        ? 'bg-red-500/20 text-red-300'
                        : 'bg-amber-500/20 text-amber-300'
                    }`}
                  >
                    {t.sentiment_level}
                  </span>
                </div>

                <div className="text-xs text-slate-400 mb-2">
                  Confidence: {t.avg_confidence}% ‚Ä¢ Impact: {t.avg_impact} ‚Ä¢ {t.count} entries
                </div>

                <div className="w-full h-2 bg-slate-700 rounded-full overflow-hidden">
                  <motion.div
                    className={`h-full ${
                      t.sentiment_level === 'Positive'
                        ? 'bg-green-500'
                        : t.sentiment_level === 'Negative'
                        ? 'bg-red-500'
                        : 'bg-amber-400'
                    }`}
                    initial={{ width: 0 }}
                    animate={{ width: `${t.avg_confidence}%` }}
                    transition={{ duration: 1.2, ease: 'easeOut' }}
                  ></motion.div>
                </div>
              </motion.div>
            ))}
          </AnimatePresence>
        </div>
      )}

      <motion.p
        className="text-xs text-slate-500 text-center mt-4"
        initial={{ opacity: 0 }}
        animate={{ opacity: 1 }}
        transition={{ delay: 0.6 }}
      >
        Live data from Supabase ‚Ä¢ Auto-updates with new intel submissions
      </motion.p>
    </motion.div>
  );
}
