'use client';
import { motion } from 'framer-motion';

export default function NeuralHalo({ status = 'listening' }) {
  const colorMap = {
    listening: 'from-blue-500 to-purple-600',
    analyzing: 'from-amber-500 to-pink-600',
    alert: 'from-red-500 to-orange-600',
  };

  return (
    <div className="relative w-40 h-40 mx-auto my-6 flex items-center justify-center">
      {/* Glowing aura */}
      <motion.div
        className={`absolute inset-0 rounded-full bg-gradient-to-r ${
          colorMap[status]
        } opacity-60 blur-2xl`}
        animate={{ scale: [1, 1.1, 1] }}
        transition={{ duration: 3, repeat: Infinity }}
      />

      {/* Core indicator */}
      <motion.div
        className="w-24 h-24 rounded-full bg-slate-900 border border-slate-700 flex items-center justify-center text-white font-semibold"
        animate={{ opacity: [0.9, 1, 0.9] }}
        transition={{ duration: 3, repeat: Infinity }}
      >
        {status === 'listening' && 'üîä Listening'}
        {status === 'analyzing' && 'üß† Thinking'}
        {status === 'alert' && '‚ö†Ô∏è Alert'}
      </motion.div>
    </div>
  );
}
